<template>
		<div class="_full_router contain-white" style="background:#eee;">
    <custHeader :title="title"></custHeader>
     <div class="query-date" style="background:#fff;">
              <div class="normal-box">
                  <div >
                      <div class="query-date-t">起始日期</div>
                        <dateSelect @callback="freshen" v-model="startDate" class="query-date-time"></dateSelect>
                    </div>
					<div><i class="fa fa-exchange" style="    font-size: 22px;color: #E40202;margin-top: 12px;"></i></div>
                   <div>
                        <div class="query-date-t">截止日期</div>
                        <dateSelect @callback="freshen" v-model="endDate" class="query-date-time"> </dateSelect>
                   </div>
              </div>
          </div>
    			 <ul style="background:#fff;margin-top:12px;" class="my-hold-container list-auto"
				 v-infinite-scroll="loadMore"
				 infinite-scroll-disabled="loading"
				 infinite-scroll-distance="10"
				 infinite-scroll-immediate-check="false"
				 >
					 <li>
                <div style="width:36vw;">成交时间</div>
                <div style="width:19vw;" class="text-right">成交价</div>
                <div style="width:20vw;" class="text-right">成交量</div>
                <div class="text-right">成交额</div>
                </li>
                <li v-for="(item,index) in qryList" :class="styleColorByBiz(item.stkBiz)">
                   <div style="width:36vw;"><div>{{item.stkName}}</div><div style="font-size:12px;">{{item.trdDate}}{{item.matchedTime}}</div></div>
                   <div style="width:19vw;" class="text-right"><div>{{item.matchedPrice | toMoney}}</div></div>
                   <div style="width:20vw;" class="text-right"><div>{{item.matchedQty }}</div></div>
                   <div class="text-right"><div>{{item.matchedAmt | toMoney}}</div><div v-remoteoptionsspan="dicts.stkBiz">{{item.stkBiz}}</div></div>
                </li>

            </ul>
            <div v-show="qryList.length==0" class="jqqd" >
                当前没有成交订单
            </div>
		</div>
</template>
<script>
import cookieUtil from 'js-cookie'
import {MessageBox,Toast} from 'mint-ui'
import custMixin from  'util/custMixin'
import custHeader from  'components/custHeader'
import dateSelect from  'components/dateSelect'
export default{
    data(){
       return {
       startDate:{val:""},
       endDate:{val:""},
	   loading: false,
       title:"历史成交",
                 qryList:[
                    ],
                 dicts:{
                      orderStatus:{
                        name:"ORDER_STATUS",
                      },
                      stkBiz:{
                        name:"STK_BIZ"
                      }
				  },
	  pars:{
				// 	sort:"orderDate",
				// 	order:"desc",
	 			limit:global.pageSize,
	 			offset:0,
	 			stkex:global.scdm,
	 			bgnDate:"",
	 			 endDate:""
		 	}
            }
    },
    created:function(){
		 var date = (new Date()).Format("yyyy-M-d");
			this.startDate={val:date.replace(/-/g,"")};
			this.endDate={val:date.replace(/-/g,"")};
      this.upCltOrderCancelQry();
    },
     components:{
    custHeader,
    dateSelect
  },
  mixins:[custMixin],
    methods:{
     freshen:function(){
		 this.pars.offset=0;
            this.upCltOrderCancelQry();
      },
        upCltOrderCancelQry:function(){
			this.pars.bgnDate= this.startDate.val;
  		  this.pars.endDate= this.endDate.val;
            axiosUtil.upCltMatchQryInfo(this.pars).then(data=>{
                var data=data.data;
                this.qryList=data.rows;
            })
        },
		loadMore:function() {
	      let self = this;
	      this.loading = true;
	      this.pars.offset  +=global.pageSize;
	      axiosUtil.upCltMatchQryInfo(this.pars).then(data=> {
	        var data = data.data;

	        this.qryList = this.qryList.concat(data.rows);

	        if (data.total == this.qryList.length) {
	          this.loading = true;
	          Toast({
	            message: "已加载最后一条",
	            position: "bottom",
	            duration: 1000
	          });
		  }else{
			    this.loading = false;
		  }

	      })
	    }
    }
}
</script>
<style type="text/css" scoped>
  .app-content{
    background: #fff;
  }
  .my-hold-container li{
    border-bottom: 1px solid #efefef;
  }
</style>



